// libs
import React from "react"
import { Provider } from "react-redux"
import { Route, Switch, Redirect } from "react-router-dom"
import getMuiTheme from "material-ui/styles/getMuiTheme"
import { DragDropContextProvider } from "react-dnd"
import HTML5Backend from "react-dnd-html5-backend"

// src
//import styles from "./App.less"



import '../server/public/css/styles/bootstrap.scss';
// custom
import '../server/public/css/styles/layout.scss';
import '../server/public/css/styles/theme.scss';
import '../server/public/css/styles/ui.scss';
import '../server/public/css/styles/app.scss';



import MUITheme from "../config/theme"
import {
  PageLogin,
  PageLogout,
  Page404,
  PageSystemView,
  PageErrorView,
  PublicRoute,
  PrivateRoute,
  NotificationSystemConnector
} from "./components"

export default class App extends React.Component {
  static propTypes = {
    userAgent: React.PropTypes.string,
    store: React.PropTypes.object,
    Router: React.PropTypes.element,
    routerProps: React.PropTypes.routerProps
  }

  static childContextTypes = {
    muiTheme: React.PropTypes.object
  }

  constructor(props) {
    super(props)
  }
  getChildContext() {
    const { userAgent } = this.props
    const theme = userAgent ? Object.assign({ userAgent }, MUITheme) : MUITheme

    return {
      muiTheme: getMuiTheme(theme)
    }
  }
  render() {
    const { store, Router, routerProps } = this.props

    return (
      <DragDropContextProvider backend={HTML5Backend}>
        <Provider store={store}>
          <Router {...routerProps}>
            <div className=''>
              <NotificationSystemConnector />
              <div className=''>
                <div className=''>

                  <div className={`container-fluid`}>
                    <Switch>
                      <PublicRoute path="/login" component={PageLogin} />
                      <Route path="/logout" component={PageLogout} />

                      <PrivateRoute
                        exact
                        path="/"
                        render={() => <Redirect to="/system" />}
                      />
                      <PrivateRoute
                        exact
                        path="/system"
                        component={PageSystemView}
                      />
                      
                      <Route exact path="/errors" component={PageErrorView} />
                      <Route component={Page404} />
                    </Switch>
                  </div>

                </div>
              </div>
            </div>
          </Router>
        </Provider>
      </DragDropContextProvider>
    )
  }
}



